body {
  align-items: center;
  background-color: var(--white);
  background: url("https://cdn.discordapp.com/attachments/1108242349940813885/1135727885467005049/254948131_2880497142248472_6622440997377344884_n-transformed.png");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: grid;
  height: 100vh;
  place-items: center;
}

.container {
  display: flex;
  height: 60vh;
}

.container__form {
  flex: 1;
  background-color: white;
  display: flex;
  padding: 0 2rem;
  border-radius: 1rem;
  border-top-right-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  margin-right: -2%;
  text-align: center;
  align-items: center;
}

.container__form .form {
  margin: auto;
  max-width: 400px; 
  width: 100%;
}

.container__form h2 {
  font-size: 2.5rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: -webkit-linear-gradient(right, rgb(78, 189, 226), rgb(60, 96, 255) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.container__form input,
.container__form textarea {
  background-color: rgb(243, 243, 243);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: black;
  margin-top: 1rem;
  width: 100%;
  padding: 0.6rem;
  border: 0;
  border-color: black;
  border-radius: 0.7rem;
  display: block;
  outline: none;
  box-sizing: border-box; /* Considerar el padding y border en el ancho del input */
}

.container__form textarea {
  height: 100px;
  resize: none;
}

.container__form button {
  margin-top: 1.5rem;
  width: 50%;
  border-radius: 1rem;
  padding: 0.6rem;
  background: -webkit-linear-gradient(right, rgb(78, 189, 226), rgb(60, 60, 255) 100%);
  font-size: 1.5rem;
  color: White;
  cursor: pointer;
  outline: none;
  border: 0;
}

.container__derecha {
  flex: 1;
  display: flex;
  background-color: #f9f9f9;
  overflow: hidden;
  border-radius: 1rem;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
}

.imagen {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (min-width: 600px) {
  .container__derecha img {
    width: 100%;
    object-fit: fill;
  }
}

@media (max-width: 599px) {
  body {
    align-items: center;
    background-color: var(--white);
    background: url("https://cdn.discordapp.com/attachments/1108242349940813885/1135727885467005049/254948131_2880497142248472_6622440997377344884_n-transformed.png");
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: grid;
    height: 100vh;
    place-items: center;
    margin-right: 1rem;
  }

  .container__derecha {
    display: none;
  }

  .container__form {
    padding: 0 3rem;
  }
  .container__form button {
    font-size: 1.1rem;
  }
}

@media (max-width: 350px ) {
  .container__form {
    padding: 1rem;
  }

  .container {
    display: flex;
    height: 70vh;
  }

  .container__form button {
    font-size: 1rem;
  }
}

@media (max-height: 700px) {
  .container {
    display: flex;
    height: 80vh;
  }

  .container__form {
    padding: 1rem;
  }
}

@media (max-height: 800px) {
  .container {
    display: flex;
    height: 80vh;
  }
}

@media (max-width: 1024px) and (max-height: 600px) {
  .container {
    display: flex;
    height: 87vh;
  }
  
  .container__form {
    padding: 0 2rem;
  }
}

@media (max-width: 1024px) and (max-height: 500px) {
  .container {
    display: flex;
    height: 90vh;
  }
  
  .container__form {
    padding: 0 1.5rem;
  }

  .container__form textarea {
    height: 50px;
    resize: none;
  }

  .container__form h2 {
    margin-bottom: 0;
    font-size: 2rem;
  }

  .container__form input {
    padding: 6px;
    margin-top: 6px;
  }

  .container__form textarea {
    padding: 6px;
    margin-top: 6px;
  }

  .container__form button {
    width: 100px;
    margin-top: 10px;
    font-size: 0.9rem;
  }
}

@media (max-width: 1024px) and (max-height: 600px) {
  .container__form h2 {
    font-size: 2rem;
  }

  .container__form button {
    font-size: 1rem;
  }
}